<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="decrement">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" :value="count" @input="updateInput($event.target.value)">

    <button type="button" aria-label="Добавить один товар" @click.prevent="increment">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
  export default {
    props: ['amount'],
    data() {
      return {
        count: this.amount,
      }
    },
    methods: {
      updateInput(value) {
        this.$emit('updateInput', value);
      },
      increment() {
        this.updateInput(this.count = this.count + 1);
      },
      decrement() {
        if (this.count > 1) this.updateInput(this.count = this.count - 1);
      },
    },
  }
</script>